* {
  font-family: 'Roboto',sans-serif; }

header {
  position: relative; }

nav {
  height: 50px;
  background-color: #212121; }

.menu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style-type: none;

  .menu_link {
    color: #ffffff;
    font-size: 15px;
    font-weight: 300; }

  .menu_item {
    border-right: 2px dotted black;
    padding-right: 28px; } }

.subheader {
  position: absolute;
  width: 100%;
  height: 84px;
  background-color: rgba(0, 0, 0, 0.6);

  .subheader_logo {
    display: block;
    max-width: 100%;
    margin-top: 16px; }

  .subheader_call {
    color: #ffffff;
    font-size: 12px;
    font-weight: 300;
    margin-top: 19px;
    text-align: right; }

  .subheader_phone {
    display: block;
    margin-top: 8px;
    color: #ffffff;
    font-size: 22px;
    font-weight: 400;
    text-transform: uppercase;
    text-align: right;
    line-height: 12px; }

  .subheader_btn {
    margin-top: 19px;
    width: 172px;
    height: 46px;
    border-radius: 4px;
    -webkit-box-sizing: inset 0 -3px rgba(0, 0, 0, 0.12);
    box-sizing: inset 0 -3px rgba(0, 0, 0, 0.12);
    border: none;
    background-image: -webkit-gradient(linear, left bottom, left top, from(#1eacc7), to(#4ce2ff));
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
    color: #ffffff;
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase; } }

.promo {
  padding: 150px 0 135px 0;
  min-height: 800px;
  background: url('../image/bg_uber.png') center (center / cover) no-repeat;

  .promo_header {
    color: #ffffff;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 60px;
    line-height: 60px;
    margin: 0;
    text-align: centet; }

  .promo_subheader {
    color: #ffffff;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 36px;
    line-height: 36px;
    margin: 0;
    margin-top: 22px;
    text-align: center; }

  .promo_descr {
    color: #ffffff;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    text-align: center;
    margin-top: 23px; }

  .promo_btn {
    display: block;
    margin: 118px auto 0 auto;
    width: 259px;
    height: 63px;
    border: none;
    border-radius: 4px;
    -webkit-box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
    box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
    border: 3px solid rgba(0, 0, 0, 0.07);
    background-image: -webkit-gradient(linear, left bottom, left top, from(#1eacc7), to(#4ce2ff));
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    color: #ffffff;
    font-size: 18px;
    font-weight: 400;
    text-transform: uppercase; } }

.start_work {
  .start_btn {
    display: block;
    border: none;
    padding: 8px 16px 8px 16px;
    margin: 70px auto 36px auto;
    background-color: #260b0b;
    font-size: 11px;
    color: #ffffff; }

  .start_text {
    padding-left: 10px;
    text-align: center;
    margin: 0 auto;
    font-size: 38px; }

  .start_block {
    padding: 40px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    min-height: 290px; }

  .start_block_text {
    margin-left: 140px;
    font-size: 17px;
    line-height: 20px;
    color: #409db4; }

  .start_block_bg {
    float: left;
    padding: 0 0 0 10px;
    width: 120px;
    height: 120px;
    border-radius: 100%;
    background-color: #1eacc7; } }

.p_text {
  margin-left: 140px; }

